	import mixPulldownRefresh from '@/components/mix-pulldown-refresh/mix-pulldown-refresh';
	import mixLoadMore from '@/components/mix-load-more/mix-load-more';

	let windowWidth = 0, scrollTimer = false, tabBar;
	var page = 1
	export default {
		components: {
			mixPulldownRefresh,
			mixLoadMore,
		},
		data() {
			return {
				tabCurrentIndex: 0, //当前选项卡索引
				scrollLeft: 0, //顶部选项卡左滑距离
				enableScroll: true,
				load:1,
				tabBars: [{
	name: '最新',
	id: '1',
}, {
	name: '推荐',
	id: '2'
}, {
	name: '热门',
	id: '3'
}],
				bkylist:[],
				bkylist1:[],
				bkylist2:[]
			}
		},
		async onLoad() {
			// 获取屏幕宽度
			windowWidth = uni.getSystemInfoSync().windowWidth;
			this.loadTabbars();
		},
		methods: {
			time_slice(str){
							var timestr = str.replace("T"," ").substring(0,19)
							return this.$options.methods.timesFun(timestr).timesString
						},
						timesFun(timesData){
			    //如果时间格式是正确的，那下面这一步转化时间格式就可以不用了
			    var dateBegin = new Date(timesData.replace(/-/g, "/"));//将-转化为/，使用new Date
			    var dateEnd = new Date();//获取当前时间
			    var dateDiff = dateEnd.getTime() - dateBegin.getTime();//时间差的毫秒数
			    var dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000));//计算出相差天数
			    var leave1 = dateDiff % (24 * 3600 * 1000)    //计算天数后剩余的毫秒数
			    var hours = Math.floor(leave1 / (3600 * 1000))//计算出小时数
			    //计算相差分钟数
			    var leave2 = leave1 % (3600 * 1000)    //计算小时数后剩余的毫秒数
			    var minutes = Math.floor(leave2 / (60 * 1000))//计算相差分钟数
			    //计算相差秒数
			    var leave3 = leave2 % (60 * 1000)      //计算分钟数后剩余的毫秒数
			    var seconds = Math.round(leave3 / 1000);
			    var timesString = '';
			
			    if (dayDiff != 0) {
			        timesString = dayDiff + '天前';
			    } else if (dayDiff == 0 && hours != 0) {
			        timesString = hours + '小时前';
			    } else if (dayDiff == 0 && hours == 0) {
			        timesString = minutes + '分钟前';
			    }
			
			    return {
			        timesString: timesString
			    }
			},
						geturl(url){
						//console.log(url.substr(38))
						
						var str = url.substr(38)
						var str1= "https://images.weserv.nl/?url="
						//console.log(str)
						//console.log(str1+str)
						return str1+str
					},
					str_slice(obj){
						var str = obj._
						if(this.$options.methods.GetLength(str)>125){
							return this.$options.methods.cutstr(str,125)
						}
					},
					str_slice1(obj){
						var str = obj._
						if(this.$options.methods.GetLength(str)>170){
							return this.$options.methods.cutstr(str,170)
						}
					},
					cutstr(str, len) {
			    var str_length = 0;
			    var str_len = 0;
			    var str_cut = new String();
			    str_len = str.length;
			    for (var i = 0; i < str_len; i++) {
			        var a = str.charAt(i);
			        str_length++;
			        if (escape(a).length > 4) {
			            //中文字符的长度经编码之后大于4  
			            str_length++;
			        }
			        str_cut = str_cut.concat(a);
			        if (str_length >= len) {
			            str_cut = str_cut.concat("...");
			            return str_cut;
			        }
			    }
			    //如果给定字符串小于指定长度，则返回源字符串；  
			    if (str_length < len) {
			        return str;
			    }
			}, 
			GetLength(str) {
			    var realLength = 0, len = str.length, charCode = -1;
			    for (var i = 0; i < len; i++) {
			        charCode = str.charCodeAt(i);
			        if (charCode >= 0 && charCode <= 128) realLength += 1;
			        else realLength += 2;
			    }
			    return realLength;
			},
			/**
			 * 数据处理方法在vue和nvue中通用，可以直接用mixin混合
			 * 这里直接写的
			 * mixin使用方法看index.nuve
			 */
			//获取分类
			loadTabbars(){
				this.loadNewsList('add');
			},
			//新闻列表
			loadNewsList(type){
				let tabItem = this.tabBars[this.tabCurrentIndex];
				
				//type add 加载更多 refresh下拉刷新
				if(type === 'add'){
					
					 uni.request({
					    url: 'https://www.qinendong.com/api/cnblogs/recent', //仅为示例，并非真实接口地址。
					    method:"POST",
						  data: {
							  id:page,
					    },
					    header: {
					        'Content-type':'application/x-www-form-urlencoded' //自定义请求头信息
					    },
					    success: (res) => {
							page++
							console.log(page)
					       //console.log(res.data.json)
							 this.items = res.data.json
							 this.bkylist = this.bkylist.concat(JSON.parse(this.items).feed.entry)
							 console.log(JSON.parse(res.data.json).feed.entry)
							//uni.hideLoading();
					    }
					});
					uni.request({
					    url: 'https://www.qinendong.com/api/cnblogs/recommend', //仅为示例，并非真实接口地址。
					    method:"POST",
						  data: {
							  id:page,
					    },
					    header: {
					        'Content-type':'application/x-www-form-urlencoded' //自定义请求头信息
					    },
					    success: (res) => {
							page++
							console.log(page)
					       //console.log(res.data.json)
							 this.items = res.data.json
							 this.bkylist1 = this.bkylist1.concat(JSON.parse(this.items).feed.entry)
							 console.log(JSON.parse(res.data.json).feed.entry)
							//uni.hideLoading();
					    }
					});
					uni.request({
					    url: 'https://www.qinendong.com/api/cnblogs/hot', //仅为示例，并非真实接口地址。
					    method:"POST",
						  data: {
							  id:page,
					    },
					    header: {
					        'Content-type':'application/x-www-form-urlencoded' //自定义请求头信息
					    },
					    success: (res) => {
							page++
							console.log(page)
					       //console.log(res.data.json)
							 this.items = res.data.json
							 this.bkylist2 = JSON.parse(this.items).feed.entry
							 console.log(JSON.parse(res.data.json).feed.entry)
							//uni.hideLoading();
					    }
					});
					
				}
				
				else if(type === 'refresh'){
					
							 uni.request({
					    url: 'https://www.qinendong.com/api/cnblogs/recent', //仅为示例，并非真实接口地址。
					    method:"POST",
						  data: {
							  id:1,
					    },
					    header: {
					        'Content-type':'application/x-www-form-urlencoded' //自定义请求头信息
					    },
					    success: (res) => {
							page++
							console.log(page)
					       //console.log(res.data.json)
							 this.items = res.data.json
							 this.bkylist = JSON.parse(this.items).feed.entry
							 
							tabItem.refreshing = false;
					    }
					});
				}
				
				
			},
			
			
			
			//下拉刷新
			onPulldownReresh(){
				this.loadNewsList('refresh');
			},
			//上滑加载
			loadMore(){
				this.loadNewsList('add');
			},
			//设置scroll-view是否允许滚动，在小程序里下拉刷新时避免列表可以滑动
			setEnableScroll(enable){
				if(this.enableScroll !== enable){
					this.enableScroll = enable;
				}
			},

			//tab切换
			async changeTab(e){
				
				console.log(e)
				console.log(tabBar)
				if(scrollTimer){
					//多次切换只执行最后一次
					clearTimeout(scrollTimer);
					scrollTimer = false;
				}
				let index = e;
				//e=number为点击切换，e=object为swiper滑动切换
				if(typeof e === 'object'){
					index = e.detail.current
					console.log("index"+index)
				}
				if(typeof tabBar !== 'object'){
					tabBar = await this.getElSize("nav-bar")
					console.log("tabBar"+tabBar)
				}
				//计算宽度相关
				let tabBarScrollLeft = tabBar.scrollLeft;
				let width = 0; 
				let nowWidth = 0;
				//获取可滑动总宽度
				for (let i = 0; i <= index; i++) {
					let result = await this.getElSize('tab' + i);
					width += result.width;
					if(i === index){
						nowWidth = result.width;
					}
				}
				if(typeof e === 'number'){
					//点击切换时先切换再滚动tabbar，避免同时切换视觉错位
					this.tabCurrentIndex = index; 
				}
				//延迟300ms,等待swiper动画结束再修改tabbar
				scrollTimer = setTimeout(()=>{
					if (width - nowWidth/2 > windowWidth / 2) {
						//如果当前项越过中心点，将其放在屏幕中心
						this.scrollLeft = width - nowWidth/2 - windowWidth / 2;
					}else{
						this.scrollLeft = 0;
					}
					if(typeof e === 'object'){
						this.tabCurrentIndex = index; 
					}
					this.tabCurrentIndex = index; 
					//第一次切换tab，动画结束后需要加载数据
					let tabItem = this.tabBars[this.tabCurrentIndex];
					
				}, 100)
				
			},
			//获得元素的size
			getElSize(id) { 
				return new Promise((res, rej) => {
					let el = uni.createSelectorQuery().select('#' + id);
					el.fields({
						size: true,
						scrollOffset: true,
						rect: true
					}, (data) => {
						res(data);
					}).exec();
				});
			},
		}
	}
